<template>
	<div class="_page">
		<Header>
			<h1 slot="title">自适应生成器</h1>
		</Header>
		<div class="computed flex-xb">
			<div class="ev source">
				<div class="head flex-yc flex-xb">
					<p>需要计算的内容</p>

					<el-button
						size="small"
						type="primary"
						icon="el-icon-setting"
						@click="switchConfig"
					>
						{{ hidenConfig ? 'OK' : '设置计算规则' }}
					</el-button>
				</div>
				<textarea
					v-show="!hidenConfig"
					v-model="oldStyle"
				></textarea>
				<div
					class="config"
					v-show="hidenConfig"
				>
					<el-form
						ref="form"
						:model="config"
						label-width="10em"
					>
						<el-form-item label="是否计算边框">
							<el-switch v-model="config.isComputedBorder"> </el-switch>
							<span style="margin-left: 20px">
								<el-tag
									v-if="config.isComputedBorder"
									type="info"
									>计算</el-tag
								>
								<el-tag v-else>不计算</el-tag>
							</span>
						</el-form-item>
						<el-form-item label="网站模式">
							<el-switch v-model="config.isSiteMode"></el-switch>
							<span style="margin-left: 20px">
								<el-tag
									v-if="config.isSiteMode"
									type="info"
									>会自动加media查询</el-tag
								>
								<el-tag v-else>不会自动加media查询</el-tag>
							</span>
						</el-form-item>
						<el-form-item
							label="便捷网站模式"
							v-show="config.isSiteMode"
						>
							<div class="input-tip">网站模式时手机端自动删除多余css</div>
							<el-switch v-model="config.removeMobCss"></el-switch>
							<span style="margin-left: 20px">
								<el-tag
									v-if="config.removeMobCss"
									type="info"
									>会自动加media查询</el-tag
								>
								<el-tag v-else>不会自动加media查询</el-tag>
							</span>
						</el-form-item>
						<el-form-item label="文字最小值">
							<div class="input-tip">小于等于这个值的文字不参与计算</div>
							<el-input v-model="config.sizeMin"> </el-input>
						</el-form-item>
						<el-form-item label="计算方法=Value ÷">
							<div class="input-tip">1920、375、自定义</div>
							<el-input v-model.trim="config.sizeComputed"> </el-input>
						</el-form-item>
						<el-form-item label="是否美化代码">
							<div class="input-tip">美化代码超过{{ this.beautifyMax }}行会严重影响效率，会自动关闭美化功能，可手动开启</div>
							<el-switch v-model="config.isBeautify"></el-switch>
							<span style="margin-left: 20px">
								<el-tag
									v-if="config.isBeautify"
									type="info"
									>美化</el-tag
								>
								<el-tag v-else>不美化</el-tag>
							</span>
						</el-form-item>
					</el-form>
				</div>
			</div>
			<div class="ev result">
				<div class="head flex-yc flex-xb">
					<p>计算结果:</p>

					<div class="flex">
						<div
							v-if="config.isSiteMode"
							class="flex"
							style="margin-right: 20px"
						>
							<el-button
								size="small"
								type="info"
								icon="el-icon-document-copy"
								@click="copyTextToClipboard(G_pcStyle)"
							>
								复制pc
							</el-button>
							<el-button
								size="small"
								type="info"
								icon="el-icon-document-copy"
								@click="copyTextToClipboard(G_mobStyle)"
							>
								复制mob
							</el-button>
						</div>

						<el-button
							size="small"
							type="primary"
							icon="el-icon-document-copy"
							@click="copyTextToClipboard($event)"
						>
							点击复制
						</el-button>
					</div>
				</div>
				<pre>
            <code id="Code" class="language-css" v-html="newStyle" ref="code"></code>
          </pre>
			</div>
		</div>

		<div :class="`tips ${tipShow ? 'show' : ''}`">✅ Copy successfully !!! 🌈</div>
	</div>
</template>

<script>
import '@/assets/css/arta.min.css';
import '@/assets/css/computed-elder.css';
import Header from '@/components/Header.vue';
import hljs from '@/assets/js/highlight.min.js';

export default {
	name: 'computedMedia',
	components: { Header },
	data() {
		return {
			oldStyle: `/*
“自适应生成器”是将代码中的px转换为vw以达到适应各种屏幕的目的

提示：
1.右上角【设置计算规则】按钮自定义计算规则
2.生成后的样式可能会影响布局
3.网站模式-生成的手机端代码需要按自己的需求进行更改
4.美化代码超过2000行会严重影响效率，会自动关闭美化功能，可手动开启
*/

/*示例：↓↓↓*/
.home {
    border-bottom: 1px solid #ECEEF2;
    padding-bottom: 7px;
}
.home .top,
.home .top .asdf{
    margin: 12px 0 9px;
    font-size: 14px;
}`,
			hidenConfig: false,
			tipShow: false,
			newStyle: '',
			firstCloseBeautify: true,
			beautifyMax: 2000,
			G_mobStyle: null,
			G_pcStyle: null,
			config: {
				sizeComputed: 1920,
				sizeMin: 1,
				isComputedBorder: false,
				isSiteMode: false, //网站模式-会自动加media查询
				removeMobCss: true,
				isBeautify: true,
			},
		};
	},
	watch: {
		oldStyle: {
			handler: function (_new, _old) {
				if (this.firstCloseBeautify) {
					var line = _new.split('\n').length;
					var max = this.beautifyMax;
					if (line > max) {
						this.$notify({
							type: 'warning',
							title: '已自动关闭代码美化',
							message: '代码超过' + max + '行，在设置项内可手动开启',
							position: 'top-right',
							duration: 1000 * 10,
							offset: 100,
						});
						this.firstCloseBeautify = false;
						this.config.isBeautify = false;
					}
				}
				this.createdNewStyle();
			},
			immediate: true,
		},
	},
	methods: {
		switchConfig() {
			this.hidenConfig = !this.hidenConfig;
			this.createdNewStyle();
		},
		createdNewStyle() {
			let styleList = this.oldStyle;
			// styleList = styleList.replaceAll("}", "}@@@");
			styleList = styleList.replace(/}/g, '}@@@');
			styleList = styleList.split('@@@');

			let allStyle = this.computedStyle(styleList);

			//网站模式
			if (this.config.isSiteMode) {
				var mobStyle = '';
				if (this.config.removeMobCss) {
					mobStyle = this.computedMobStyle(styleList);
				}
				this.G_mobStyle = mobStyle;
				this.G_pcStyle = allStyle;
				allStyle = `/* pc */
@media screen {
${allStyle}
}
/* mob */
@media screen and (max-width:768px) {
${mobStyle}
}
`;
			}

			if (this.config.isBeautify) {
				allStyle = hljs.highlight(allStyle, { language: 'css' }).value;
			}
			this.$nextTick(() => {
				this.newStyle = allStyle;
			});
		},
		/**
		 * 计算px=>vw
		 * 返回计算后的样式
		 */
		computedStyle(styleList) {
			let allStyle = '';
			styleList.forEach((styleEv) => {
				// 加上前缀
				// styleEv = styleEv.replace(/.*(-,|{)/g, ($1) => {
				//   // 忽略媒体查询
				//   if ($1.indexOf('@media') != -1) {
				//     return $1;
				//   }
				//   // 忽略动画
				//   if ($1.indexOf('@keyframes') != -1) {
				//     return $1;
				//   }
				//   // 忽略动画内部的from，to,0%,25%...
				//   if (/^[from|to|\d]/g.test($1.trim())) {
				//     return $1;
				//   }
				//   // console.log($1);
				//   let res = $1.trim();
				//   let trimLength = $1.length - res.length;
				//   let trimStr = '';
				//   for (var i = 0; i < trimLength; i++) {
				//     trimStr += ' ';
				//   }
				//   return trimStr + res;
				// });
				styleEv = styleEv.replace(/(?<={)(.*\s*(?!}))*/g, ($1) => {
					// 拆分，获取到每个属性
					// let arr = $1.replaceAll(";", ";@@@").split("@@@");
					let arr = $1.replace(/;/g, ';@@@').split('@@@');
					let str = '';
					arr.forEach((line, i) => {
						if (line.indexOf('font-size') != -1) {
							str += computedPX(line, this.config.sizeComputed, this.config.sizeMin);
						} else if (line.indexOf('border') != -1) {
							if (!this.config.isComputedBorder) {
								str += line;
								return;
							}
							str += computedPX(line, this.config.sizeComputed);
						} else {
							str += computedPX(line, this.config.sizeComputed);
						}
					});
					return str;
				});
				allStyle = allStyle + styleEv;
			});

			return allStyle;
			/*
			 * line 要查找的目标，例如：margin: 12px 0 9px;
			 * formula 计算的数值，例如 1920
			 * minSize 最小值，如果小于最小值则使用最小值
			 */
			function computedPX(line, formula, minSize) {
				if (minSize) {
					return line.replace(/(\d+px)/g, ($1) => {
						if (parseFloat($1) < minSize) {
							return minSize + 'px';
						}
						return Math.round(((parseFloat($1) / formula) * 100000).toFixed(4)) / 1000 + 'vw';
					});
				}
				return line.replace(/(\d+px)/g, ($1) => {
					return Math.round(((parseFloat($1) / formula) * 100000).toFixed(4)) / 1000 + 'vw';
				});
			}
		},
		/**
		 * 计算手机端样式，删除多余css
		 * 返回计算后的样式
		 */
		computedMobStyle(styleList) {
			let mobStyle = '';
			styleList.forEach((styleEv) => {
				// console.log(styleEv);
				styleEv = styleEv.replace(/(?<={)(.*\s*(?!}))*/g, ($1) => {
					// 拆分，获取到每个属性
					let arr = $1.replace(/;/g, ';@@@').split('@@@');
					let str = '';
					arr.forEach((line, i) => {
						if (line.indexOf('px') != -1 || line.indexOf('%') != -1 || line.indexOf('{') != -1 || line.indexOf('}') != -1 || line.indexOf('flex') != -1) {
							str += line;
						}
					});
					return str;
				});

				mobStyle = mobStyle + styleEv;
			});
			return mobStyle;
		},

		copyTextToClipboard(text) {
			let that = this;
			this.tipShow = false;
			// let text = this.newStyle;
			text = text || that.$refs.code.innerText;
			if (!navigator.clipboard) {
				function fallbackCopyTextToClipboard(text) {
					// 1.创建一个可选中元素
					let textArea = document.createElement('textarea');
					textArea.value = text;
					// 2.使用定位，阻止页面滚动
					textArea.style.top = '0';
					textArea.style.left = '0';
					textArea.style.position = 'fixed';
					document.body.appendChild(textArea);
					textArea.focus();
					textArea.select();
					try {
						var successful = document.execCommand('copy');
						var msg = successful ? 'successful' : 'unsuccessful';
						// console.log('Fallback: Copying text command was ' + msg);
						that.tipShow = true;
						var iii = setTimeout(() => {
							that.tipShow = false;
							clearTimeout(iii);
						}, 1000);
					} catch (err) {
						console.error('Fallback: Oops, unable to copy', err);
					}
					// 3.移除元素
					document.body.removeChild(textArea);
				}
				fallbackCopyTextToClipboard(text);
				return;
			}
			navigator.clipboard.writeText(text).then(
				function () {
					// console.log('Async: Copying to clipboard was successful!');
					that.tipShow = true;
				},
				function (err) {
					console.error('Async: Could not copy text: ', err);
				}
			);
		},
	},
};
</script>
